.order{
    position: relative;
    z-index: 1000;
    height: 100vh;
    width: 100vw;
    background: var(--bg_modal_screen);
    position: fixed;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    top: 0;
    pointer-events: none;
    transition: 0.5s;
    color: var(--text_color);
    &-content{
        position: fixed;
        text-align: center;
        max-width: 35rem;
        left: 50%;
        top: 80px;
        background-color: #ededed;
        border-radius: 4px;
        transform: translateX(-50%) !important;
        padding: 3rem 6rem 4rem 6rem;   
    }
    &-close {
        position: absolute;
        top: -0.5rem;
        right: -0.5rem;
        font-size: 5rem;
        width: 5rem;
        height: 5rem;
        line-height: 5rem;
        text-align: center;
        background: transparent;
        color: var(--btn_close);
        border: none;
    }
    &-design{
        z-index: 999;
        
        position: fixed;
        background: var(--bg_modal_screen);
        &-design h4 {
            margin-bottom: 3.5rem;
          }
    }
}
.order.active{
    opacity: 1;
    pointer-events: all;
    transform: scale(1);
} 

////////////////////////////////////////////////////////////////////////
  .button {
    border-radius: 50px;
    display: block;
    margin: 0 auto;
  
    &-order {
        background: var(--gradient_linear);
        box-shadow: 0px 15px 30px 0px rgba(170, 38, 183, 0.39);
        font-size: 1.5rem;
        padding: 1.5rem 2rem;
        color: var(--text_color_btn);
        border: 2px solid #ba1ab0;
        background-color: transparent;
        width: 100%;
        margin-top: 1rem;
        &:hover{
            color: var(--btn_hover);
        }
    }
  }
//////////////////////////////////////////////////////////////////////////
  .order-design .form {
    margin: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
  .order-design .form input,
  .order-design .form textarea {
    border: none;
    border-radius: 50px;
    background-color: #ffffff;
    padding: 0.8rem;
    margin-bottom: 1.5rem;
    width: 100%;
    color: inherit;
    resize: none;
  }
  .order .main-form{
    margin-top: 25px;
  }
  .order-design .form input::placeholder,
  .order-design .form textarea::placeholder {
    color: var(--text_color);
    text-align: center;
  }

  



  


