@font-face {
  font-family: 'icons';
  src:  url('fonts/icons.eot?flq5kb');
  src:  url('fonts/icons.eot?flq5kb#iefix') format('embedded-opentype'),
    url('fonts/icons.ttf?flq5kb') format('truetype'),
    url('fonts/icons.woff?flq5kb') format('woff'),
    url('fonts/icons.svg?flq5kb#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icons' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

