
.accordion{
    &-header{
    padding: 1rem;
    width: 100%;
    background: var(--main_color);
    &-title{
    margin: 0;
    color: var( --text_color);
    }
    &-item{
        
        cursor: pointer;
        display: grid;
        justify-content: center;
        grid-template-columns: 1fr 1fr;
        font-size: 25px;
        align-items: center;
        
        i{
            transition: 0.5s;
            color: var( --text_color);
            margin-right: 0; 
        }
        i.active{
            transition: 0.5s;
            transform: rotate(90deg);
        }
    }
    }
    &-content{
        background-color: var(--accordion_grey);
        padding: 1rem;
    }
    &-content.active{
        transition: 0.5s;
        opacity: 1;
    }
}