@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;700;900&display=swap');

:root {
    --main_color: #c67e12;
    --text_color: #000000;
    --text_color_btn: #ffffff;
    --nav-color-bg: #424449;
    --link-color_light: white;
    --accordion_grey:  #CBCCC4;
    --bg_modal_screen: rgba(0, 0, 0, 0.4);
    --bg_footer: #0f1a2b;
    --gradient_linear: linear-gradient(45deg, #614AD3, var(--main_color));
    --gradient: linear-gradient(to bottom, #614AD3, #E42C64);
}


*{
    margin: 0 auto;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    overflow-y:scroll;
    
}
.container{
    //max-width: 90vw;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
}



