.MainPage {
    background-color: rgb(20, 13, 9);
    width: 100%;
    height: 100vh;
    
    .wrapper {
        height: 100vh;
        /*This part is important for centering*/
        display: grid;
        place-items: center;
      }
      
      .typing-demo {
        color:coral;
        white-space: nowrap;
        overflow: hidden;
        border-right: 3px solid;
        font-family: monospace;
        font-size: 2em;
      }
        @keyframes coloring {
            1%{
                color:coral;
            }
        

        }
      @keyframes typing {
        from {
          width: 0;
        }
        
      }
          
      @keyframes blink {
        50% {
          border-color: transparent
        }
      }
}
