.aboutme{
    margin: 0;/*обнуление отступов*/
    padding: 0;/*обнуление полей*/
    box-sizing: border-box;/*метод расчета размеров блока*/
    &__wrraper{
        height: 100%;
        width: 100%;
        color: black;
        display: flex;
    &__title{
        font-weight: bold;
        font-size: 15px;
    }
    &__description{
        margin-top: 15px;
    }
    &__img{
        img {
        max-width: 250px;
        margin: 20px;
        clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
        transition: all 1s;
        }
        
        img:hover {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
    }
        
    }
}