.portfolio{
    display: block;
    &__wrapper{
    display: grid;
    /*
    * auto-fit - create columns only if there's content for them
    * auto-fill - create columns even if they will be empty
    */
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 1rem;
    height: 100%;
        &__card{
            display: flex;
            background-color: white;
            padding: 1rem;
            border-radius: 10px;
            &__link{
                cursor: pointer;
                color: black;
                text-decoration: none;
            }
            &__img{
                max-width: 500px;
                max-height: 100%;
                overflow: hidden;
                border-radius: 10px;
                img{
                    max-height: 100%;
                    max-width: 100%;     
                }
            }
            &__description{
                display: grid;
                text-transform: uppercase;
                width: 100%;
                ul{ 
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    display: grid;
                    gap: 0.5rem;
                    
                    //padding: 1em;
                    grid-template-columns: repeat(auto-fit, minmax(75px, 1fr));
                    li{
                        display: flex;
                        gap: 10px;
                        span{
                            font-size: 10px;
                            border: darkblue dashed;
                            background: white;
                            border-radius: 100px;
                            position: relative;
                            padding: 0.25rem;
                            &:hover{
                                color: black;
                                
                                background: rgba(0,0,0, 0.1);
                            }
                        }
                    }
                
                   
                }
            }
            &:hover{
                transform: translateY(-15px);
                box-shadow: 12px 12px 2px 1px rgba(0, 0, 255, .2);
                color:var(--main-color);
            .portfolio__wrapper__card__title{
                    color: var(--main_color);
                }
            }
        }
    }
}