.footer-conatiner {
    background: var(--bg_footer); 
    padding: 4rem;
}

.footer{
    width: 80%;
    height: 20vh;
    color:  #fff;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
    background-color: var(--bg_footer);
}
.footer-heading {
    display: flex;
    flex-direction: column;
    margin-right: 4rem;
    h2 {
        margin-bottom: 2rem;
    }
    a{
        color:  #fff;
        text-decoration: none;
        margin-bottom: 0.5rem;
        &:hover {
            color: blueviolet;
            transition: 0.3s ease-out;
        }
    }
}
.footer-last-form h2{
    margin-bottom: 2rem;
}

#footer-button{
    width: 100%;
    height: 40px;
    border-radius: 15px;
    background: var(--gradient);
}

