@import "./components/animation/animation.scss";
.tehnologi {
    &__subtitle{
        margin-top: 30px;
    }
    &__items {
        margin: 0 auto;
        padding: 0 10px;
        display: grid;
        justify-content: center;
        grid-template: repeat(2, minmax(270px, auto)) / repeat(auto-fit, 280px);
        grid-auto-rows: minmax(270px, auto);
        gap: 13px;
        
    }
    &__item {
        width: 100%;
        transition: all 0.5s;
        border: 1px solid black;
        padding: 30px 30px 25px 30px;
        &-img {
            transform: scale(1.5);
            display: flex;
            align-items: center;
            min-height: 65px;
        }
        h3 {
            margin-top: 20px;
            margin-bottom: 0;
        }
        p {
            margin-top: 15px;
            margin-bottom: 0;
            font-size: 12px;
            line-height: 18px;
        }

        &:hover{
            z-index: 5;
            transform: scale(130%);
            background-color: rgb(255, 230,230);
            box-shadow: 25px 25px 25px #000;
        }
        
    }
}