@import "./animation/animation.scss";
:root {
    --main_color_btn: #c67e12;
    --btn_gradient_1: #614AD3;
    --btn_gradient_2: #03a9f4;
    --btn_gradient_3: #d34040;
    --btn_gradient_4: #240b36;
    --btn_bg_gradient: linear-gradient(90deg, var( --btn_gradient_1), var(--btn_gradient_3), var( --btn_gradient_4),  var( --btn_gradient_1));
}  
.button {
    position: relative;
    border-radius: 30px;
    display: block;
    margin: 0 auto;
    &-order {
        background: var(linear-gradient(45deg, var(--star_btn_gradient), var(--main_color)));
        box-shadow: 0px 15px 30px 0px rgba(170, 38, 183, 0.39);
        font-size: 1.5rem;
        padding: 1.5rem 2rem;
        border: 2px solid #ba1ab0;
        background-color: transparent;
        width: 100%;
        margin-top: 1rem;
        background-position:right;
    }
    &-effects{
        color: var(--text_color_btn);
        transform: transfer(-50%, -50%);
        background: var(--btn_bg_gradient);
        background-size: 400%;
        border-radius: 30px;
        z-index: 1;
        &:hover{
            text-decoration: none;
            animation: default-btn-animated 8s linear infinite;
        }
        &:before{
            content: '';
            position: absolute;
            top: -5px;
            left: -5px;
            bottom: -5px;
            right: -5px;
            z-index: -1;
            background: var(--btn_bg_gradient);
            background-size: 400%;
            border-radius: 40px;
            filter: blur(20px);
            opacity: 0;
            transition: 0.5s;
        }
        &:hover:before{
            filter: blur(20px);
            opacity: 1;
            @include animated;
            animation: default-btn-animated 8s linear infinite;
        }
    }
    
}



