.nav{
    
    &-link
    {
        a{
            color: var(--link-color);
            text-decoration: none;
            height: 100%;
            display: flex;
            align-items: center;
            padding: .25rem;
        }
       
    }

    &-link:hover
    {
        a{
            color: var(--main_color);
            border-radius: 5px;
            background: rgba(255,255,255, 0.1);
        } 
    }
}